<script>
import { mapState } from 'vuex';

export default {
  name: 'Sheet',
  props: {
  },
  computed: mapState({
    sheet: state => state.sheet.activeSheet,
    sheetId: state => state.sheet.activeSheetId,
    userId: state => state.user.user.uid,
    weekString(){
        if(this.sheet.week){
            let weekParts = this.sheet.week.split('-W');
            let startDate = this.getDateOfWeek(weekParts[1],weekParts[0]),
                startDateString = (startDate.getMonth()+1) + '/' + startDate.getDate() + '/' + startDate.getFullYear();
            startDate.setDate(startDate.getDate()+7);
            let endDateString = (startDate.getMonth()+1) + '/' + startDate.getDate() + '/' + startDate.getFullYear();
            return startDateString + ' - ' + endDateString;
        }
        
        return '';
    }
  }),
  components: {
  },
  beforeMount(){
  },
  created: function(){
  },
  data: function(){
    return {
        week:'',
        strategyToggle:{},
        pieceToggle:{},
        temp:{
            show:false
        }
    }
  },
  methods: {
    updatePieceNotes(index){
        this.$store.dispatch('sheet/updatePieceNotes',{
            uid:this.userId,
            sheetId:this.sheetId,
            pieceIndex:index,
            notes:event.target.value
        });
    },
    test(){
        event.preventDefault();
        this.temp.show = !this.temp.show;
    },
    getDateOfWeek(w, y) {
        var d = (1 + (w - 1) * 7); // 1st of January + 7 days for each week
        return new Date(y, 0, d);
    },
    weekChange(el){
        this.$store.dispatch('sheet/updateWeek',{uid:this.userId,sheetId:this.sheetId,week:el.target.value});
    },
    handleStrategyToggle(ref){
        this.strategyToggle[ref] = !this.strategyToggle[ref];
        this.strategyToggle = {... this.strategyToggle};
    },
    handlePieceToggle(ref){
        this.pieceToggle[ref] = !this.pieceToggle[ref];
        this.pieceToggle = {... this.pieceToggle};
    },
    formatDescription(description){
        description = description.replace(/\n/g,'<br />');
        return description;
    },
    updateTime(sheetIndex,measureIndex,strategyIndex,ind){
            //let sheetCopy = { ...this.sheet };
            //move this logic to mutation state?
            if(measureIndex !== null){
                if(!this.sheet[sheetIndex].measures[measureIndex].strategies[strategyIndex].practice){
                    this.sheet[sheetIndex].measures[measureIndex].strategies[strategyIndex].practice = {};
                }      
                if(!this.sheet[sheetIndex].measures[measureIndex].strategies[strategyIndex].practice[ind]){
                    this.sheet[sheetIndex].measures[measureIndex].strategies[strategyIndex].practice[ind] = {};
                }
                this.sheet[sheetIndex].measures[measureIndex].strategies[strategyIndex].practice[ind].time = event.target.value;
            }
            else {
                if(!this.sheet[sheetIndex].strategies[strategyIndex].practice){
                    this.sheet[sheetIndex].strategies[strategyIndex].practice = {};
                }
                if(!this.sheet[sheetIndex].strategies[strategyIndex].practice[ind]){
                    this.sheet[sheetIndex].strategies[strategyIndex].practice[ind] = {};
                }
                this.sheet[sheetIndex].strategies[strategyIndex].practice[ind].time = event.target.value;
            }
            this.$store.dispatch('sheet/updateMeasurePracticeTime',
            {
                uid:this.userId,
                sheetId:this.sheetId,
                sheet:this.sheet[sheetIndex],
                sheetRef:sheetIndex
            });
    }
  }
}

</script>

