const state = {
    activeSheet:{},
    activeSheetId:''
}

import * as firebase from 'firebase'

var database;

  // getters
const getters = {}

// actions
const actions = {
    addEntrySheet({ commit }, entry){
        if(!database){
            database = firebase.database();
        }
        let newSheetRef = database.ref('users/' + entry.userUID + '/sheets').push();
        //initialize week
        var d = new Date();
        d.setDate(d.getDate() + (1 + 7 - d.getDay()) % 7);
        let weekParts = getWeekNumber(d);

        entry.entry.week = weekParts[0]+'-W'+weekParts[1];

        //find next monday and get week
        newSheetRef.set(entry.entry);
        commit('setActiveSheetId',newSheetRef.key);
        commit('setActiveSheet',entry.entry);
        return newSheetRef.key;
    },
    updateWeek({ commit }, info){
        if(!database){
            database = firebase.database();
        }
        database.ref('users/' + info.uid + '/sheets/' + info.sheetId).update({
            week:info.week
        });
        commit('updateWeek',info.week);
    },
    updatePieceNotes({ commit }, info){
        if(!database){
            database = firebase.database();
        }
        database.ref('users/' + info.uid + '/sheets/' + info.sheetId + '/' + info.pieceIndex).update({
            notes:info.notes
        });
        commit('updatePieceNotes',{index:info.pieceIndex,notes:info.notes});
    },
    updateMeasurePracticeTime({ commit }, info){
        if(!database){
            database = firebase.database();
        }
        let {sheetRef,sheet } = info;
        database.ref('users/' + info.uid + '/sheets/' + info.sheetId + '/' + sheetRef).update(sheet);
    }
}

// mutations
const mutations = {
    setActiveSheet (state, activeSheet) {
      state.activeSheet = activeSheet;
    },
    setActiveSheetId (state, id){
        state.activeSheetId = id;
    },
    updateWeek (state, week){
        state.activeSheet.week = week;
    },
    updatePieceNotes (state, info){
        state.activeSheet[info.index].notes = info.notes;
    }
}


function getWeekNumber(d) {
    // Copy date so don't modify original
    d = new Date(Date.UTC(d.getFullYear(), d.getMonth(), d.getDate()));
    // Set to nearest Thursday: current date + 4 - current day number
    // Make Sunday's day number 7
    d.setUTCDate(d.getUTCDate() + 4 - (d.getUTCDay()||7));
    // Get first day of year
    var yearStart = new Date(Date.UTC(d.getUTCFullYear(),0,1));
    // Calculate full weeks to nearest Thursday
    var weekNo = Math.ceil(( ( (d - yearStart) / 86400000) + 1)/7);
    // Return array of year and week number
    return [d.getUTCFullYear(), weekNo];
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
  }
